import React, { useState, useEffect } from 'react';
import classnames from "classnames";
import {
  CardBody,
  CardTitle,
  Container,
  Row,
  Col,
  Nav,
  NavItem,
  NavLink,
  TabContent,
  TabPane,
  Card
} from "reactstrap";
import { useSelector, useDispatch } from 'react-redux';
import { getTranscripts, updateTranscript } from 'store/aws/actions';
import { getParticipants } from 'store/zoom/actions';
import RecordingList from './RecordingList';
import InlineEdit from "components/inlineEdit";
import HighlightButtonsGroup from 'components/recordings/HighlightButtonsGroup';
import CommentBox from 'components/recordings/CommentBox';
import { saveSelection, restoreSelection } from 'utils/selection-utils';
import Comment from 'components/recordings/Comment';

function CalendarView({ location }) {
  const dispatch = useDispatch();

  const [state, setState] = useState({
    activeTab: 1,
    hiddenCommentBox: true,
    hiddenButtonGroup: true,
    highlightBtnsGroupLayout: {
      position: 'absolute',
      left: '0',
      top: '0',
      heightInPixel: 26,
      widthInPixel: 74
    },
  });

  const [selectedRange, setSelectedRange] = useState();
  const user = useSelector(state => state.users.user);
  const token = useSelector(state => state.users.token);
  const zoomAccessToken = useSelector(state => state.zoomData.zoomAccessToken);
  const transcripts = useSelector(state => state.awsData.transcripts);
  const meeting = { ...location.state };
  const { hash } = location;

  useEffect(() => {
    if (Object.keys(meeting).length !== 0) {
      dispatch(getParticipants(token, zoomAccessToken, meeting.uuid));
      dispatch(getTranscripts(token, user.id, meeting.uuid));
    }
  }, [location]);

  const toggle = tab => {
    const { activeTab } = state;
    if (activeTab !== tab) {
      setState({ ...state, activeTab: tab });
    }
  }

  const updateSpeaker = (text, index) => {
    const { dialog, speakersCount } = transcripts;

    let updatedDialog = dialog.map((script, i) => {
      if (i % speakersCount === index) {
        script.speaker = text
        return script;
      } else {
        return script;
      }
    });

    // update transcripts
    dispatch(updateTranscript(token, user.id, meeting.uuid, { ...transcripts, dialog: updatedDialog }));
  };

  // Comment and Highlight
  const bubbleUpEditableSelectedRegion = (e) => {
    const arrowEvents = ['ArrowDown', 'ArrowUp', 'ArrowLeft', 'ArrowRight'];
    if (arrowEvents.includes(e.key) && e.shiftKey) {
      bubbleUpSelectedRegion(e);
    }
  }

  const bubbleUpSelectedRegion = (e) => {

    const selection = window.getSelection();

    if (selection.toString()) {
      const range = selection.getRangeAt(0);
      const rect = range.getBoundingClientRect();

      setBtnsGroupPosition(rect);
    } else {
      setState({ ...state, hiddenButtonGroup: true });
    }
  }

  const setBtnsGroupPosition = ({ left, top, width, height }) => {
    const { heightInPixel, widthInPixel } = state.highlightBtnsGroupLayout;
    const computedLeft = left + (width / 2) - (widthInPixel / 2);
    const computedTop = window.scrollY + top - heightInPixel - 6;

    setState({
      ...state,
      highlightBtnsGroupLayout: {
        ...state.highlightBtnsGroupLayout,
        left: computedLeft,
        top: computedTop
      },
      hiddenButtonGroup: false
    })
  }

  const toggleState = (updateState) => {
    setState(prevState => ({ ...prevState, [updateState]: !state[updateState] }));
  }

  const toggleCommentBox = () => {
    toggleState('hiddenCommentBox');
  }

  // Could be used outside click of HighlightButtonsGroup
  const toggleButtonsGroup = () => {
    toggleState('hiddenButtonGroup');
  }

  const saveAndRestoreSelection = () => {
    const savedSelection = saveSelection();
    setSelectedRange(savedSelection);
    toggleCommentBox();
    restoreSelection(savedSelection);
  }

  console.log(selectedRange)

  return (
    <>
      {
        
        hash === '' && Object.keys(meeting).length === 0 ?
          <RecordingList />
          :
          <>
            <Container className="mt-0" fluid>
              <h1 className="main-page-title">Recordings</h1>
            </Container>
            <Container className="mt-0" fluid>
              <h2 className="BigTitle">{meeting.topic}</h2>
              <Row>
                <Col md="6" xl="3">
                  <Card className="card-stats">
                    <CardBody>
                      <Row>
                        <div className="col">
                          <CardTitle
                            tag="h5"
                            className="text-uppercase mb-0"
                          >
                            Date
                    </CardTitle>
                          <span className="h2 font-weight-bold zoom-meeting-top-title">
                            11/12/2020
                    </span>
                        </div>
                      </Row>
                    </CardBody>
                  </Card>
                </Col>
                <Col md="64" xl="3">
                  <Card className="card-stats">
                    <CardBody>
                      <Row>
                        <div className="col">
                          <CardTitle
                            tag="h5"
                            className="text-uppercase mb-0"
                          >
                            duration
                          </CardTitle>
                          <span className="h2 font-weight-bold zoom-meeting-top-title">
                            3:40
                          </span>
                        </div>

                      </Row>
                    </CardBody>
                  </Card>
                </Col>
                <Col md="6" xl="3">
                  <Card className="card-stats">
                    <CardBody>
                      <Row>
                        <div className="col">
                          <CardTitle
                            tag="h5"
                            className="text-uppercase mb-0"
                          >
                            Sentiment
                          </CardTitle>
                          <span className="h2 font-weight-bold zoom-meeting-top-title green-title-main">0.5</span>
                        </div>
                      </Row>
                    </CardBody>
                  </Card>
                </Col>
                <Col md="6" xl="3">

                </Col>
              </Row>

              <Row className="all-participant-outer-row">
                <Col md="12" xl="12">
                  <div className="participants-row">
                    <div className="participatnt-column">
                      <strong>PARTICIPANTS</strong>
                    </div>
                    <div className="participatnt-column">
                      <div className="avatar-group text-center">
                        <img className="avatar avatar-md rounded-circle mb-2"
                          alt="..."
                          src={require("assets/img/theme/team-1.jpg")}
                        /> <span className="tb-title">Ryan Tompson</span>
                      </div>
                    </div>
                    <div className="participatnt-column">
                      <div className="avatar-group">
                        <img className="avatar avatar-md rounded-circle"
                          alt="..."
                          src={require("assets/img/theme/team-2.jpg")}
                        /> <span className="tb-title">Vicky Brown</span>
                      </div>
                    </div>
                    <div className="participatnt-column">
                      <div className="avatar-group">
                        <img className="avatar avatar-md rounded-circle"
                          alt="..."
                          src={require("assets/img/theme/team-3.jpg")}
                        /> <span className="tb-title">Kevin Miller</span>
                      </div>
                    </div>
                    <div className="participatnt-column">
                      <div className="avatar-group">
                        <img className="avatar avatar-md rounded-circle"
                          alt="..."
                          src={require("assets/img/theme/team-4.jpg")}
                        /> <span className="tb-title">Mike Abreu</span>
                      </div>
                    </div>
                    <div className="participatnt-column">
                      <div className="avatar-group">
                        <img className="avatar avatar-md rounded-circle"
                          alt="..."
                          src={require("assets/img/theme/team-5.jpg")}
                        /> <span className="tb-title">John Adams</span>
                      </div>
                    </div>
                    <div className="participatnt-column">
                      <div className="avatar-group">
                        <img className="avatar avatar-md rounded-circle"
                          alt="..."
                          src={require("assets/img/theme/team-1.jpg")}
                        /> <span className="tb-title">Carlos Arias</span>
                      </div>
                    </div>
                  </div>
                </Col>
              </Row>
              <Row>
                <Col md="9" xl="9">
                  <Nav tabs className="left-custom-tabs">
                    <NavItem>
                      <NavLink
                        className={classnames({ active: state.activeTab === '1' })}
                        onClick={() => toggle('1')}>
                        Transcript
                      </NavLink>
                    </NavItem>
                    <NavItem>
                      <NavLink
                        className={classnames({ active: state.activeTab === '2' })}
                        onClick={() => toggle('2')}>
                        Media File
                      </NavLink>
                    </NavItem>
                  </Nav>
                  <TabContent activeTab={state.activeTab}>
                    <TabPane tabId="1">
                      {state.activeTab == 1 ?
                        <div className="tab-inner-content"
                          onMouseUp={bubbleUpSelectedRegion}
                          onMouseMove={bubbleUpSelectedRegion}
                          onKeyUp={bubbleUpEditableSelectedRegion}
                        >
                          {
                            transcripts && transcripts.dialog && transcripts.dialog.length > 0 &&

                            transcripts.dialog.map((transcript, index, arr) => {
                              const time = Math.round(transcript.time);
                              const min = Math.floor(time / 60);
                              const sec = time % 60;

                              return (
                                <Row className="transcript_row" key={index}>
                                  <Col xl="2">
                                    <div className="transcript-left-col">
                                      <div className="avatar-group">
                                        <img className="avatar avatar-sm rounded-circle"
                                          alt="..."
                                          src={require("assets/img/theme/team-1.jpg")}
                                        />
                                        <span className="transcript-user-title">
                                          <InlineEdit
                                            text={transcript.speaker}
                                            onSetText={text => updateSpeaker(text, index % transcripts.speakersCount)}
                                          />
                                        </span>
                                      </div>
                                      <span className="transcript-time">Time: <strong>{min > 9 ? min : '0' + min}:{sec > 9 ? sec : '0' + sec}</strong></span>
                                      <span className="sentiment-title">Sentiment</span>
                                      <ul className="sentiment-progress-bar">
                                        <li></li>
                                        <li></li>
                                        <li></li>
                                        <li><span>0.5</span></li>
                                      </ul>
                                    </div>

                                  </Col>
                                  <Col xl="10" id="transcripts">
                                    <p className="color-para" data-index={index} dangerouslySetInnerHTML={{
                                      __html: transcript.line
                                    }}></p>
                                  </Col>
                                </Row>
                              )
                            })
                          }
                        </div>
                        : null}
                    </TabPane>
                    <TabPane tabId="2">
                      {state.activeTab == 2 ?
                        <div className="tab-inner-content">
                          <div className="embed-responsive embed-responsive-16by9">
                            <iframe className="embed-responsive-item" src="https://www.youtube.com/embed/jbPpdyn16sY" allowFullScreen></iframe>
                          </div>
                          <span>CAPTIONS</span>
                          <h4>Anna Lee</h4>
                          <p>Lorem Ipsum is simply dummy text of the printing and typesetting industry. Lorem Ipsum has been the industry's standard dummy text ever since the 1500s, when an unknown printer took a galley of type and scrambled it to make a type specimen book.</p>
                        </div>
                        : null}
                    </TabPane>
                  </TabContent>
                </Col>
                <Col md="3" xl="3">
                  <div className="custom-sidebar">
                    <div className="custom-sidebra-header">
                      <ul>
                        <li className="li-active">All</li>
                        <li>Comments</li>
                        <li>Highlight</li>
                      </ul>
                      <div className="custom-card">
                        <div className="custom-card-body">
                          <i className="fas fa-ellipsis-v"></i>
                          <span>Anna Lee</span>
                          <p>Lorem Ipsum is simply dummy text of the printing ..</p>
                        </div>
                        <span className="custom-card-footer">Highlighted - 6:30PM Nov 11</span>
                      </div>
                      
                      {
                        transcripts && transcripts.comments && transcripts.comments.length > 0 &&

                        transcripts.comments.map((comment, index) => (
                          <Comment comment={comment} key={index} />
                        ))}
                      <CommentBox
                        hidden={state.hiddenCommentBox}
                        selectedRange={selectedRange}
                        userId={user.id}
                        meetingUUID={meeting.uuid}
                        transcripts={transcripts}
                        toggleCommentBox={toggleCommentBox}
                        toggleButtonsGroup={toggleButtonsGroup}
                      />
                    </div>
                  </div>
                </Col>
              </Row>
            </Container>

            <HighlightButtonsGroup
              layout={state.highlightBtnsGroupLayout}
              hidden={state.hiddenButtonGroup}
              saveAndRestoreSelection={saveAndRestoreSelection}
            />
          </>
      }

    </>
  );
}

export default CalendarView;

